	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter Blog","author":"Kyle Mathews"}},"markdownRemark":{"id":"/Users/macbookair/WebstormProjects/omermindivanli.com/src/pages/Ömer Mindivanli Blog Page/index.md absPath of file >>> MarkdownRemark","html":"<p>This is my first blog! How exciting!</p>\n<p>I’m sure I’ll write a lot more interesting things in the future.</p>\n<p>Oh, and here’s a great quote from this Wikipedia on\n<a href=\"http://en.wikipedia.org/wiki/Salted_duck_egg\">salted duck eggs</a>.</p>\n<blockquote>\n<p>A salted duck egg is a Chinese preserved food product made by soaking duck\neggs in brine, or packing each egg in damp, salted charcoal. In Asian\nsupermarkets, these eggs are sometimes sold covered in a thick layer of salted\ncharcoal paste. The eggs may also be sold with the salted paste removed,\nwrapped in plastic, and vacuum packed. From the salt curing process, the\nsalted duck eggs have a briny aroma, a gelatin-like egg white and a\nfirm-textured, round yolk that is bright orange-red in color.</p>\n</blockquote>\n<p><img src=\"./salty_egg.jpg\" alt=\"Chinese Salty Egg\"></p>","frontmatter":{"title":"I have started to publish blog posts","date":"March 20, 2018"}}},"pathContext":{"slug":"/Ömer Mindivanli Blog Page/","previous":{"fields":{"slug":"/hi-folks/"},"frontmatter":{"title":"New Beginnings"}},"next":false}}

